import Vue from "vue";
import Vuex from "vuex";
import user from "./modules/user";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isMobile: false,
  },
  mutations: {
    MOBILE(state, isMobile) {
      state.isMobile = isMobile;
    },
  },
  actions: {},
  modules: {
    user,
  },
});
