<template>
  <div id="app" :class="'app' + (mobile ? '_APP' : '_PC')">
    <el-container>
      <!-- 头部-PC -->
      <el-header v-if="!mobile" class="animate__animated animate__fadeIn">
        <div class="logo">
          <img v-lazy="require('./assets/img/top_logo.png')" class="logo_img" />
        </div>
        <el-menu :default-active="defaultActive" mode="horizontal" :router="router" text-color="#141414"
          active-text-color="#16A5A6">
          <el-menu-item v-for="(item, index) in pageList" :key="index" :index="item.index">
            {{ item.name }}
          </el-menu-item>
        </el-menu>
        <div class="header_phone">
          <img v-lazy="require('./assets/img/header_phone.png')" class="header_phone_icon" />
          <span class="header_phone_title">咨询热线</span>
          <span class="header_phone_txt">
            13051525673
          </span>
        </div>
      </el-header>
      <!-- 头部-APP -->
      <el-header v-if="mobile" class="animate__animated animate__fadeIn">
        <div class="logo">
          <img v-lazy="require('./assets/img/top_logo.png')" class="logo_img" />
        </div>
        <div class="icon_switch_box">
          <i class="el-icon-s-operation icon_switch" @click="switchMenuFunc"></i>
        </div>
      </el-header>
      <el-main style="min-height: 800px;">
        <router-view />
      </el-main>
      <!-- 底部 -->
      <div class="footer">
        <div class="footer_img">
          <img v-lazy="require('./assets/img/message_right_img_1.png')" />
          <img class="footer_img_r" v-lazy="require('./assets/img/message_right_img_2.png')" />
        </div>
        <div class="footer_message">
          <div class="footer_message_txt">
            公司名称：北京人人通信息咨询有限公司
          </div>
          <div class="footer_message_txt">
            公司地址：北京市门头沟区新桥大街2号2幢5层518/河北省三河市燕郊东方夏威夷Y栋4单元2门472
          </div>
          <div class="footer_message_txt">
            联系电话：13051525673/13522408320
          </div>
        </div>
      </div>
    </el-container>
    <el-drawer :visible.sync="drawer" :with-header="false" append-to-body>
      <el-menu :default-active="defaultActive" :router="router" text-color="#141414" active-text-color="#16A5A6">
        <el-menu-item v-for="(item, index) in pageList" :key="index" :index="item.index">
          {{ item.name }}
        </el-menu-item>
      </el-menu>
    </el-drawer>
  </div>
</template>

<script>
import './style/PC/App.scss'
import './style/APP/App.scss'
export default {
  data() {
    return {
      router: true,
      drawer: false,
      defaultActive: "/",
      pageList: [
        { name: "首页", index: "/" },
        { name: "关于我们", index: "/companyProfile" },
        { name: "服务内容", index: "/businessLine" },
        { name: "客户案例", index: "/cooperationCases" },
        { name: "行业资讯", index: "/honorQualification" },
        { name: "联系我们", index: "/contactUs" },
      ],
    };
  },
  methods: {
    monitorPlatform() {
      const isMobile = document.body.clientWidth <= 765
      if (isMobile != this.$store.state.isMobile) {
        this.$store.commit('MOBILE', isMobile)
      }
    },
    switchMenuFunc() {
      this.drawer = true
    }
  },
  destroyed() {
    window.onresize = null
  },
  created() {
    this.monitorPlatform()
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        this.monitorPlatform()
        this.drawer = false
      })()
    }
  },
  watch: {
    $route(to) {
      this.defaultActive = to.path;
    },
  },
  beforeCreate() {
    /* eslint-disable */
    let count = 0;
    let imgs = [
      //用require的方式添加图片地址，直接添加图片地址的话，在build打包之后会查找不到图片，因为打包之后的图片名称会有一个加密的字符串
      require("./assets/resource/关于我们.jpg"),
      require("./assets/resource/专注服务.jpg"),
      require("./assets/resource/合作案例.jpg"),
      require("./assets/resource/行业资讯.jpg"),
      require("./assets/resource/联系我们.jpg"),
    ];
    for (let img of imgs) {
      let image = new Image();
      image.onload = () => {
        count++;
      };
      image.src = img;
    }
  },
};
</script>